.dropdown-card {
  position: absolute;
  top: 40px; /* Adjust to position below the display icon */
  left: 0; /* Adjust to align with the left side of the icon */
  width: 200px;
  padding: 10px;
  background-color: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  z-index: 10;
}

.dropdown-card label {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.dropdown-card select {
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #ccc;
  margin-top: 5px;
}
